.nav {
  &__list {
    display: flex;
  }
  
  &__item {
    margin-left: 20px;
  
    &:first-child {
      margin-left: 0;
    }
  }
  
  &__link {
    position: relative;
    padding: 23px 0;
  
    @include flexAlign(center);
    flex-wrap: nowrap;
    white-space: nowrap;
  
    @extend %text-normal;
    font-style: normal;
    text-transform: uppercase;
    text-decoration: none;
    color: $color-accent-dark;
    transition: color $duration-time $timing-function;
  
    &:hover {
      color: $color-main;
    }

    &--is-active {
      color: $color-main;
    
      &::after {
        content: " ";
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        height: 4px;
        border-radius: 2px;
        background-color: $color-main;
      }
    }
  }
}
